import { callUploadApi } from '../../resource/uploadOSSApi'
export default {
  name: 'uploadButton',
  props: {
    isImage: {
      type: Boolean,
      default: false
    },
    isShowPesent: {
      type: Boolean,
      default: false
    },
    text: {
      type: String,
      default: '点击上传'
    }
  },
  data() {
    return {
      filePath: '',
      fileList: [],
      copyFile: ''
    }
  },

  methods: {
    progress(percent) {
      this.fileList[this.fileList.length - 1].percent = percent
      if (percent === 100) {
        setTimeout(() => {
          this.fileList[this.fileList.length - 1].isComplate = true
        }, 500)
      }
      this.$refs.file.value = null
    },
    onFileCopyClick(index) {
      this.$refs.input.value = this.fileList[index].filePath
      this.$refs.input.focus()
      const canCopy = document.execCommand('copy')
      if (canCopy) {
        this.$refs.input.select()
        document.execCommand('copy')
        this.$message({
          message: '复制链接成功',
          type: 'success'
        })
      }
    },
    onCloseFileClick(index) {
      this.fileList.splice(index, 1)
    },
    onUploadClick() {
      this.$refs.file.click()
    },
    onFileChange(event) {
      const file = event.target.files[0]
      if (file === undefined) {
        return
      }
      this.fileList.push({
        name: file.name,
        id: Math.random() * 1000,
        isActive: true,
        percent: 0,
        isComplate: false,
        filePath: ''
      })
      // this.$callApi({
      //   api: 'get_first_data',
      //   param: {},
      //   mock: true
      // }).then(data => {
      //   this.fileList[this.fileList.length - 1].filePath = data.filePath
      //   this.fileList[this.fileList.length - 1].percent = data.percent
      //   if (data.percent === 100) {
      //     setTimeout(() => {
      //       this.fileList[this.fileList.length - 1].isComplate = true
      //     }, 500)
      //   }
      //   this.$refs.file.value = null
      // })
      callUploadApi(
        file,
        data => {
          this.fileList[this.fileList.length - 1].filePath = data
        },
        this.progress
      )
    }
  }
}



